export const dataStore = {
    goodData: null,
    reservData: null,
    goodAndReservData: null,
    weekendsData: null,

    setGoodData(goods) {
        this.goodData = goods;
    },

    setReservData(reserves) {
        this.reservData = reserves;
    },

    setGoodAndReservData(data) {
        this.goodAndReservData = data;
    },

    setWeekendsData(weekends) {
        this.weekendsData = weekends;
    },

    getGoodData() {
        return this.goodData;
    },

    getReservData() {
        return this.reservData;
    },

    getGoodAndReservData() {
        return this.goodAndReservData;
    },

    getWeekendsData() {
        return this.weekendsData;
    },
};