<template>
  <div class="weekends-container">
    <v-sheet class="weekends-container-sheet">
      <v-calendar
          class="weekends-container-calendar"
          style="background-color: snow"
          ref="calendar"
          v-model="value"
          :events="events"
          :view-mode="type"
          :weekdays="weekday"
          :locale="'ru'"
      ></v-calendar>
    </v-sheet>
  </div>
</template>

<script>
import {dataStore} from "@/operations/requests";
export default {
  name: "WeekendsComponent",
  data: () => ({
    type: 'month',
    weekday: [1, 2, 3, 4, 5, 6, 0],
    value: [new Date()],
    events: [],
    employersData: [],
  }),
  methods: {
    getEmployersData(){
      let fullWeekendsData = [...dataStore.getWeekendsData()];
      for (let i = 0; i < fullWeekendsData.length; i++) {
        this.employersData.push(fullWeekendsData[i])
      }
    },
    showEvents() {
      const events = [];

      this.employersData.forEach(employer => {
        const start = this.convertDateFormat(employer.Start);
        const end = this.convertDateFormat(employer.End);

        let currentDate = start;
        while (currentDate <= end) {
          events.push({
            title: employer.Name,
            start: new Date(currentDate),
            end: new Date(currentDate),
            color: employer.Color,
            allDay: true,
          });
          currentDate.setDate(currentDate.getDate() + 1);
        }
      });

      this.events = events;
    },
    convertDateFormat(dateString) {
      const [day, month, year] = dateString.split('.');
      return new Date(year, month - 1, day);
    },
  },
  mounted() {
    this.getEmployersData();
    this.showEvents();
    localStorage.setItem('checkLoadStatus', 'loaded');
  },
  beforeUnmount() {
    localStorage.removeItem('checkLoadStatus');
  }
}
</script>


<style scoped>

.weekends-container {
  width: 95%;
  margin: 0 auto;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  padding: 30px;
  background-color: snow;

  .weekends-container-sheet{

    .weekends-container-calendar{

    }
  }
}
</style>

